@use "global" as *;


.sns-wrap {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: rem(40);
}

.sns-wrap__insta {

}

.sns-wrap__insta img {
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    aspect-ratio: 1 / 1;
    max-width: rem(48);
    height: rem(48);
  }
}

.sns-wrap__insta:hover img {  // hover
  opacity: 0.6;
}

.sns-wrap__line {

}

.sns-wrap__line img {
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    aspect-ratio: 1 / 1;
    max-width: rem(48);
    height: rem(48);
  }
}

.sns-wrap__line:hover img {  // hover
  opacity: 0.6;
}




// =======================================
// モディファイア
// =======================================
// sns-wrap--footer-sp (SP時のみ表示)
.sns-wrap--footer-sp.sns-wrap {
  display: none;

  @include mq(md) {
    display: flex;
  }
}



// sns-wrap--footer-pc (PC時のみ表示)
.sns-wrap--footer-pc.sns-wrap {
  gap: rem(22.79);

  @include mq(md) {
    display: none;
  }
}

.sns-wrap--footer-pc.sns-wrap {

}

.sns-wrap--footer-pc .sns-wrap__insta img {
  max-width: rem(48);
  height: rem(48);

  @include mq(md) {
    aspect-ratio: 1 / 1;
    max-width: rem(48);
    height: rem(48);
  }
}

.sns-wrap--footer-pc .sns-wrap__line img {
  max-width: rem(48);
  height: rem(48);

  @include mq(md) {
    aspect-ratio: 1 / 1;
    max-width: rem(48);
    height: rem(48);
  }
}