@use "global" as *;


.btn-reserve01 {
  display: block;
  padding-block: rem(18);
  padding-inline: rem(42.59);
  background: $color-font-base-1;
  position: relative;

  @include mq(xl) {
    padding-block: rem(15);
    padding-inline: rem(20);
  }
}

.btn-reserve01::after {  // グラデーションの背景色
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(216deg, #D59481 -4.1%, #402E32 96.92%);
  position: absolute;
  inset: 0;
  transition: opacity 0.3s ease-out;
}

.btn-reserve01:hover::after {  // hover
  opacity: 0;
}

.btn-reserve01__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(8.82);
  position: relative;
  z-index: 30;

  @include mq(xl) {
    gap: rem(10);
  }
}

.btn-reserve01__icon {
  aspect-ratio: 18 / 20;
  width: rem(18);
  display: grid;
  place-items: center;

  @include mq(xl) {
    width: rem(16);
  }
}

.btn-reserve01__icon svg {
  @include mq(xl) {
    width: rem(14);
  }
}

.btn-reserve01__text {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;
}
