@use "global" as *;


.footer {
  background: $color-background-base-2;
  padding-top: rem(40.72);
  padding-bottom: rem(41.49);
  position: relative;

  @include mq(md) {
    padding-top: rem(64.87);
    padding-bottom: rem(170);
  }
}

.footer__inner {

}

.footer__wrap {

}

.footer__top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: rem(19.63);

  @include mq(md) {
    align-items: flex-start;
    gap: rem(0);  // 余白をmargin-topで管理する
  }
}

.footer__title {

}

.footer__info {
  @include mq(md) {
    margin-top: rem(32.65);
  }
}

.footer__sns-sp {
  @include mq(md) {
    margin-top: rem(38);
  }
}

.footer__nav {
  margin-top: rem(20.74);
}

.footer__bottom {
  margin-top: rem(24.2);  // (56.2 - 32) カンプの間隔 - 『nav__item』のpadding
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include mq(md) {
    margin-top: rem(32.27);
    flex-direction: column-reverse;
    justify-content: center;
    gap: rem(40);
  }
}

.footer__bottom-left {

}

.footer__bottom-text {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(12);
  line-height: 1.6;
}

.footer__google-policy {
  border-bottom: 1px solid $color-white;
  transition: opacity 0.2s ease-out;
}

.footer__google-policy:hover {  // hover
  opacity: 0.7;
}

.footer__terms {
  border-bottom: 1px solid $color-white;
  transition: opacity 0.2s ease-out;
}

.footer__terms:hover {  // hover
  opacity: 0.7;
}

.footer__copyright {
  display: block;
  margin-top: rem(40);
  margin-left: rem(11.6);

  color: $color-white;
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(16);
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  @include mq(md) {
    margin-top: rem(32);
    margin-left: 0;
  }
}

.footer__bottom-right {

}

.footer__sns-pc {

}

.footer__policy {
  display: block;
  width: fit-content;
  margin-left: auto;
  margin-top: rem(37.47);

  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(12);
  line-height: 1.6;
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-decoration-skip-ink: none;
  text-decoration-thickness: auto;
  text-underline-offset: auto;
  text-underline-position: from-font;
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    margin-top: 0;
  }
}

.footer__policy:hover {  // hover
  opacity: 0.7;
}

.footer__top-btn {
  width: fit-content;
  position: absolute;
  top: rem(64);
  right: calc(50% - min(48vw, 39.68rem));  // *位置を制御
}
