@use "global" as *;


.is-fixed {  // ドロワーが開いている際にスクロール出来ないようにするクラス。jsで『html,body』に付与。
  overflow: hidden;
}

.header {
  height: rem(96);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500;
  transition: transform 0.3s;transition: transform 0.3s;

  @include mq(md) {
    height: rem(88);
  }
}

.header__inner {
  height: 100%;
  padding-inline: rem(64);
  max-width: calc(rem(1312) + (rem(64)) * 2);  // 『padding-inline』の分も考慮 (合計1440pxに)
  margin-inline: auto;

  @include mq(md) {
    position: relative;
  }
}

.header__wrap-pc {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mq(md) {
    display: none;
  }
}

.header__title {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;
  transition: opacity 0.1s ease-out;
}

.header__title:hover {
  opacity: 0.7;
}

.header__title-link {
  display: inline-block;
  text-align: center;
}

.header__title-br {
  display: none;

  @include mq(xl) {
    display: inline-block;
  }
}

.header__nav {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(48);

  @include mq(md) {
    display: none;
  }
}

.header__link {
  height: 100%;
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.4;
  display: grid;
  place-items: center;
  position: relative;
}

.header__link::after {  // hover時の伸びる下線 (通常は『scaleX(0)』で非表示)
  content: "";
  width: 100%;
  height: 1px;
  background: $color-white;
  position: absolute;
  bottom: rem(30);
  left: 0;
  transform: scaleX(0);
  transform-origin: right bottom;
  transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
}

.header__link:hover::after {  // hover (下線が伸びる)
  transform: scaleX(1);
  transform-origin: left bottom;
}



.header__btn {  // ハンバーガーメニュー のボタン
  display: none;

  @include mq(md) {
    display: block;
    aspect-ratio: 1 / 1;
    width: rem(88);
    height: rem(88);
    border-radius: 0px 0px 0px rem(72);
    background: $color-white;
    box-shadow: -2px 8px 8px 0px rgba(0, 0, 0, 0.08);

    padding-top: rem(23.63);
    padding-bottom: rem(39.64);
    padding-left: rem(36);
    padding-right: rem(20);

    position: absolute;
    top: 0;
    right: 0;
    z-index: 100;
  }
}

.header__btn-wrap {
  width: rem(32);
  height: rem(24.7);
  position: relative;
}

.header__btn.is-clicked {

}

.header__btn.is-clicked .header__bar:nth-of-type(1),
.header__btn.is-clicked .header__bar:nth-of-type(3) {
  @include mq(md) {
    top: rem(12.35);
  }
}

.header__btn.is-clicked .header__bar:nth-of-type(1) {
  @include mq(md) {
    transform: rotate(45deg);
  }
}

.header__btn.is-clicked .header__bar:nth-of-type(2) {
  @include mq(md) {
    opacity: 0;
  }
}

.header__btn.is-clicked .header__bar:nth-of-type(3) {
  @include mq(md) {
    transform: rotate(-45deg);
  }
}


.header__bar {
  @include mq(md) {
    display: inline-block;
    width: 100%;
    height: 2.91px;
    border-top: 2.91px solid $color-font-base-3;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    transition: transform 0.3s ease-out, top 0.3s ease-out, opacity 0.2s ease-out;
  }
}

.header__bar:nth-of-type(2) {
  @include mq(md) {
    top: rem(10.91);
  }
}

.header__bar:nth-of-type(3) {
  @include mq(md) {
    top: rem(21.82);
  }
}



/* スクロールした時に『header2』を表示するためのクラス名(JSで制御) */
.header--hidden {
  transform: translateY(-100%);
}
