@use "global" as *;


.title01 {
  display: block;
}

.title01__text {
  display: none;

  @include mq(md) {
    display: block;
    color: $color-white;
    leading-trim: both;
    text-edge: cap;
    font-family: $font-family-ja-1;
    font-size: rem(12);
    font-weight: $bold;
    line-height: 1.4;
  }
}

.title01__img {

  @include mq(md) {
    margin-top: rem(16);
  }
}

.title01__img img {

  @include mq(md) {
    aspect-ratio: 204.185 / 63.691;
    width: rem(204.185);
  }
}




// =======================================
// モディファイア
// =======================================
// title01--footer
.title01--footer {
  transition: opacity 0.2s ease-out;
}

.title01--footer:hover {  // hover
  opacity: 0.7;
}

.title01--footer .title01__text {
  display: none;

  @include mq(md) {
    display: block;
  }
}

.title01--footer .title01__picture {
  @include mq(md) {
    display: block;
    margin-top: rem(16);
  }
}

.title01--footer .title01__img {
  @include mq(md) {
    margin-top: 0;
  }
}

.title01--footer img {
  aspect-ratio: 1 / 1;
  width: rem(139.102);

  @include mq(md) {
    aspect-ratio: 204.185 / 63.691;
    width: rem(204.185);
    max-width: rem(204.185);
  }
}
