@use "global" as *;


.nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.nav__item {
  display: block;
  width: 100%;
  padding-block: rem(32);
  border-bottom: 1px solid #7B695A;

  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(20);
  font-style: normal;
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;
}

.nav__item:last-of-type {
  border-bottom: none;
}




// =======================================
// モディファイア
// =======================================
// nav--footer
.nav--footer.nav {
  border-top: 1px solid #7B695A;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  gap: rem(15);  // 最低限の余白を指定

  @include mq(md) {
    margin-top: rem(38.54);
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 0;
  }
}

.nav--footer .nav__item {
  width: fit-content;
  border-bottom: none;
  padding-block: rem(34);
  position: relative;

  @include mq(md) {
    display: block;
    width: 100%;
    padding-block: rem(32);

    color: $color-white;
    leading-trim: both;
    text-edge: cap;
    font-family: $font-family-en-1;
    font-size: rem(20);
    font-style: normal;
    font-weight: $bold;
    line-height: 1.4;
    letter-spacing: 0.04em;
    text-indent: 0.04em;
    text-transform: capitalize;

    border-top: 1px solid #7B695A;
  }
}

.nav--footer .nav__item:nth-of-type(1) {  // 先頭の要素は『border-top』を非表示に。
  @include mq(md) {
    border-top: none;
  }
}

.nav--footer .nav__item::after {  // hover時の伸びる下線 (通常は『scaleX(0)』で非表示)
  content: "";
  width: 100%;
  height: 1px;
  background: $color-white;
  position: absolute;
  bottom: rem(27);
  left: 0;
  transform: scaleX(0);
  transform-origin: right bottom;
  transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);

  @include mq(md) {
    display: none;
  }
}

.nav--footer .nav__item:hover::after {  // hover (下線が伸びる)
  transform: scaleX(1);
  transform-origin: left bottom;
}
