@use "global" as *;


.drawer-menu {
  display: none;

  @include mq(md) {
    display: block;
    padding-top: rem(24.16);
    padding-bottom: rem(45);
    overflow: scroll;  // スマホ横向けの時にメニューがはみ出る際にスクロールして閲覧可能に。
    background: $color-background-base-2;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 400;
    transform: translateY(-200%);  // 最初は上に隠れている。
    transition: transform 0.4s linear;
  }
}

.drawer-menu.is-clicked {
  transform: translateY(0);
}

.drawer-menu__inner {
  @include mq(md) {
    padding-inline: rem(24);
    max-width: calc(rem(345) + (rem(24)) * 2);  // 『padding-inline』の分も考慮 (合計375pxに)
    margin-inline: auto;
  }
}

.drawer-menu__wrap {
  @include mq(md) {

  }
}

.drawer-menu__top {
  @include mq(md) {

  }
}

.drawer-menu__nav {
  @include mq(md) {
    margin-top: rem(8.65);
  }
}

.drawer-menu__bottom {
  @include mq(md) {
    margin-top: rem(8.5);
  }
}

.drawer-menu__info {
  @include mq(md) {

  }
}

.drawer-menu__sns-wrap {
  @include mq(md) {
    margin-top: rem(25);
  }
}
