@use "global" as *;


.top-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: rem(18);
}

.top-btn:hover .top-btn__arrow {  // hover
  transform: translateY(rem(-5));
}

.top-btn__arrow {
  transition: transform 0.2s ease-out;
}

.top-btn__arrow img {
  aspect-ratio: 10 / 75;
  width: rem(10);
  height: rem(75);
}

.top-btn__text {
  color: $color-white;
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.0;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  // transform: rotate(90deg);
  writing-mode: vertical-rl; /* 縦書きに変更 */
  width: rem(18); /* 横幅を適切に設定 */
}
