@use "global" as *;


.is-fixed {  // ドロワーが開いている際にスクロール出来ないようにするクラス。jsで『html,body』に付与。
  overflow: hidden;
}

.header2 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5000;

  background: rgba(255, 255, 255, 0.64);
  backdrop-filter: blur(7px);

  padding-block: rem(20);

  // display: none;  // 通常時は非表示。(JSで制御)
  transform: translateY(-100%); /* 上からスライドインするための初期位置 */
  transition: transform 0.3s ease; /* スムーズなスライドイン */

  @include mq(md) {
    display: none;
  }
}

.header2__inner {
  padding-left: rem(24.4);
  padding-right: rem(32.7);
}

.header2__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: rem(40);  // 念の為に指定
}

.header2__logos {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(13.88);
  transition: opacity 0.2s ease-out;
}

.header2__logos:hover {  // hover
  opacity: 0.6;
}

.header2__logo-01 {
  width: fit-content;
}

.header2__logo-01 img {
  aspect-ratio: 53 / 70;
  width: rem(53);
}

.header2__logo-02 {
  width: fit-content;
}

.header2__logo-02 img {
  aspect-ratio: 164 / 50;
  width: rem(164);
}

.header2__right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: rem(50.8);

  @include mq(xl) {
    gap: rem(38);
  }
}

.header2__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(48);

  @include mq(xl) {
    gap: rem(35);
  }
}

.header2__link {
  color: $color-font-base-3;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.004em;
  text-indent: 0.004em;
  text-transform: capitalize;
  position: relative;
}

.header2__link::after {  // hover時の伸びる下線 (通常は『scaleX(0)』で非表示)
  content: "";
  width: 100%;
  height: 1px;
  background: $color-font-base-3;
  position: absolute;
  bottom: rem(-5);
  left: 0;
  transform: scaleX(0);
  transform-origin: right bottom;
  transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
}

.header2__link:hover::after {  // hover (下線が伸びる)
  transform: scaleX(1);
  transform-origin: left bottom;
}

.header2__reserve {
  max-width: rem(176);
}

.header2__btn-reserve {

}




.header2--visible {
  // display: block;
  transform: translateY(0); /* スライドインして表示 */
}
