@use "global" as *;


.info {

}

.info__address {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(14);
  font-weight: $normal;
  font-style: normal;
}

.info__tel {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  font-style: normal;
  margin-left: rem(-10);
}




