  }
}

.info--footer .info__address {
  color: $color-white;
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  font-weight: $normal;

  @include mq(md) {
    font-size: rem(14);
    text-align: left;
  }
}

.info--footer .info__tel {
  color: $color-white;
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  font-weight: $normal;

