@use "global" as *;


.scroll-bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: rem(50);
}

.scroll-bar__text {
  color: #7B695A;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(16);
  font-style: normal;
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  transform: rotate(90deg);
}

.scroll-bar__bar {
  width: 1px;
  height: rem(87.668);
  background: $color-white;
  overflow: hidden;
  position: relative;
  margin-left: rem(-5);  // 位置調整

  @include mq(md) {
    height: rem(165);
    margin-left: rem(-2);  // 位置調整
  }
}

.scroll-bar__bar::after {  // スクロールバーの、動く色の部分
  content: "";
  display: block;
  width: 1px;
  height: rem(87.668);
  background: $color-background-base-2;
  position: absolute;
  bottom: 0;
  left: 0;

  animation: scroll 2.5s ease-in-out infinite normal;

  @include mq(md) {
    height: rem(165);
  }
}


/* 『スクロール』アニメーション定義 */
@keyframes scroll {
  0% {
      transform: translateY(-100%); /* 画面外上部に位置 */
  }
  15% {
      transform: translateY(0%); /* 中央に移動 */
  }
  30% {
      transform: translateY(100%); /* 画面外下部に移動 */
  }
  100% {
      transform: translateY(100%); /* 同じ位置を維持 */
  }
}
