@use "global" as *;


.reserve-circle {
  display: block;
  aspect-ratio: 1 / 1;
  border-radius: 50%;
  border: 1px solid $color-white;  // 白枠部分 (外枠)
  background: rgba(255, 255, 255, 0.20);

  @include mq(md) {
    position: fixed;
    bottom: rem(56.67);
    right: calc(50% - min(48vw, 40rem));  // *位置を制御
  }
}

.reserve-circle__brown {  // 茶色部分 (内側)
  border-radius: 50%;
  max-width: rem(186.94);
  width: 100%;
  aspect-ratio: 1 / 1;
  background: rgba($color-font-base-1, 0.8);  // 透明度も付与。

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include mq(md) {
    max-width: rem(93.057);
  }
}

.reserve-circle__brown::after {  // グラデーションの背景色
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  aspect-ratio: 1 / 1;
  // background: linear-gradient(216deg, #D59481 -4.1%, #402E32 96.92%);
  background: linear-gradient(216deg, rgba(213, 148, 129, 0.8) -4.1%, rgba(64, 46, 50, 0.8) 96.92%);  // 透明度も付与。
  // mix-blend-mode: multiply;
  position: absolute;
  inset: 0;
  // z-index: 20;
  transition: opacity 0.3s ease-out;
}

.reserve-circle__brown:hover::after {  // hover
  opacity: 0;
}

.reserve-circle__en {  // 『Reserve』
  color:#B9A89B;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  margin-top: rem(-20);  // 位置調整
  position: relative;
  z-index: 25;

  @include mq(md) {
    font-size: rem(11);
    margin-top: rem(-5);  // 位置調整
  }
}

.reserve-circle__ja {  // 『予約する』
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(25);
  font-weight: $bold;
  line-height: 1.4;

  position: relative;
  z-index: 25;

  @include mq(md) {
    font-size: rem(14);
  }
}

.reserve-circle__icon {
  width: rem(33.385);
  height: rem(35.95);

  position: relative;
  z-index: 25;

  @include mq(md) {
    width: rem(20.8);
    height: rem(22.533);
  }
}

.reserve-circle__icon svg {
  max-width: rem(33.385);
  max-height: rem(35.95);
  width: 100%;
  height: 100%;

  margin-bottom: rem(-30);  // 位置調整

  @include mq(md) {
    max-width: rem(20.8);
    max-height: rem(22.533);
    margin-bottom: rem(-6);  // 位置調整
  }
}
